<template>
    <div>
        <div class="wrap">
            <div class="topContainer">
                <van-nav-bar title="耀莱成龙影城（建业店）" left-arrow :border="false" @click-left="toCinema">
                    <template #right >
                        <van-icon name="star-o" size="16" @click="starThis" v-show="!isStar" />
                        <van-icon name="star" size="16" @click="starThis" v-show="isStar" />
                    </template>
                </van-nav-bar>
                <div class="cell">
                    <van-icon name="location-o" />
                    <div class="title" @click="toMap">
                        <p>耀莱成龙影城（建业店）</p>
                        <p>金水区中州大道建业置地</p>
                    </div>
                    <van-icon name="arrow"></van-icon>
                </div>
                <swiper ref="detailSwiper" :options="swiperOptions">
                    <swiper-slide v-for="(item,index) in imgListL" :key="index" class="per">
                        <!-- <img :src="item" alt=""> -->
                        <!-- <img src="@/assets/images/cinema/1.png" alt=""> -->
                        <img :src="item" alt="">
                        <!-- <img :src="`@/assets/images/cinema/${index+1}.png`" alt=""> -->
                        <div class="ratio">
                                <van-icon name="star" size="4" />
                                <van-icon name="star" size="4" />
                                <van-icon name="star" size="4" />
                                <van-icon name="star" size="4" />
                                <van-icon name="star" size="4" />
                            <span><span style="font-size:30px">9</span>.5</span>
                        </div>
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                </swiper>
                </div>
                <div>
                    
                <div class="centerCont">
                    <p>后来的我们</p>
                    <p>119分钟 | 剧情 | 周冬雨</p>
                </div>
                <div class="lastCont">
                    <p>今天2月1日</p>
                    <div class="timeCont" v-for="item in 4" :key="item">
                        <div>
                            <p>15:30</p>
                            <p>17:14散场</p>
                        </div>
                        <div>
                            <p>原版3D</p>
                            <p>2号厅（冠名招商中）</p>
                        </div>
                        <div>
                            <p><span>30.9</span>元</p>
                        </div>
                        <div>
                            <div @click="toTicket">购票</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import img1 from '@/assets/images/cinema/1.png'
import img2 from '@/assets/images/cinema/2.png'
import img3 from '@/assets/images/cinema/3.png'
import img4 from '@/assets/images/cinema/4.png'
import img5 from '@/assets/images/cinema/5.png'
export default {
    data() {
        return {
            isStar: '',
            swiperOptions: {
                slidesPerView: 3,
                spaceBetween: -80,
                centeredSlides: true,
                loop: true,
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                }
            },
            imgListL: [
                    img1,img2,img3,img4,img5
                ]
        }
    },
    methods: {
        starThis() {
            if(this.$store.state.cinema.starList.has(1)) {
                this.$store.state.cinema.starList.delete(1)
            } else {
                this.$store.commit('cinema/addStarItem', 1)
            }
            this.isStar = this.$store.state.cinema.starList.has(1)
        },
        toMap() {
            this.$router.push('/map')
        },
        toCinema() {
            this.$router.push('/cinemalist')
        },
        toTicket() {
            this.$router.push('/cticket')
        }
    },
    computed: {
    },
    created() {
        this.$store.state.currentPage = this.$router.currentRoute.fullPath
        this.isStar = this.$store.state.cinema.starList.has(1)
        // console.log(this.$refs)
        // this.$refs.detailSwiper.$el.style.width = '288px'
    },
    mounted() {
        setTimeout(() => {
            document.querySelector('.swiper-container').style.width = '288px'
        }, 30)
    }
}
</script>

